.widgetSm{
    flex: 1;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    padding: 20px;
    margin-right: 20px;
}
.widgetSmTitle{
    font-size: 20px;
    font-weight: 600;
}
.widgetSmImg{
    font: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}
.widgetSmList{
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.widgetSmListItem{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 0px;
}
.widgetSmUser{
    display: flex;
    flex-direction: column;
}
.widgetSmUsername{
    font-weight: 600;
}
.widgetSmUserTitle{
    font-weight: 200;
    color: #555;
    font-size: 11px;
}
.widgetSmButton{
    font-size: 10px;
    display: flex;
    align-items: center;
    border: none;
    border-radius: 10px;
    padding:  10px;
    background-color: #eeeef7;
    color: #555;
    cursor: pointer;
}
.widgetSmIcon{
    margin-right: 5px;
    font-size: 16px!important;
}