.widgetLg{
    flex: 2;
    box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
    padding: 20px;
}
.widgetLgTitle{
    font-size: 20px;
    font-weight: 600;
}
.widgetLgTable{
    width: 100%;
    border-spacing: 20px;

}
.widgetLgTh{
    text-align: left;
}
.widgetLgUser{
    display: flex;
    align-items: center;
    font-weight: 600;
}
.widgetLgImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.widgetLgDate,.widgetLgAmount{
    font-weight: 100;
    font-size: 12px;
}

.widgetLgButtonApproved{
    background-color: #7bb2ab;
    color: darkgreen;
    
}
.widgetLgButtonDeclined{
    background-color: rgb(221, 142, 142);
    color: darkred;
}
.widgetLgButtonPending{
    background-color: wheat;
    color: darkslateblue;
}
.widgetLgButtonApproved,.widgetLgButtonDeclined,.widgetLgButtonPending{
    padding: 5px 7px;
    border: none;
    border-radius: 10px;
}